.sidebar{
    height:100vh;
    width:100%;
    background-color:#ffd9be7d;
    overflow-y: scroll;
    text-align: center;
}
    
.sidebar > h1 {
    padding-top: 20px;
    text-align: center;
}

.sidebar-logo {
  display: flex;
}

.h1_links{
  text-decoration: none;
  font-family: 'Aclonica', sans-serif;
  color:rgba(2, 3, 14, 0.822);
  font-size:38px;
}

.h1_links:hover {
  border: #00000060;
  border-radius: 5px;
  border-bottom: 12px double #051f0028;
  color: rgb(247, 111, 0);
}

.sidebar > p {
    margin-top: 30px;
    text-align: center;
}

.sidebar-nav{
    margin-left: -30px;
    margin-top: 30px;
    text-align: center;
}
.sidebar-nav-items{
    margin-bottom: 15px;
}
.sidebar>img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-top: 40px;
  display: flex;
}

.links{
  text-decoration: none;
  font-family: 'Quicksand', sans-serif;
  font-family: 'Comfortaa', cursive;
  color:#000;
  font-size: larger;
  line-height: 1.4rem;
}
.sidebar-nav-icons{
    margin-bottom: 15px;
}

.flip-card-back{
  margin-top:50px;
}
.fa-lg{
  text-decoration: none;
  color:red;
}

button {
	-webkit-transition-duration: 0.1s;
	transition-duration: 0.1s;
	background-color: #fff8f4;
	border-radius: 8px;
	border-bottom: 4px solid #21274b;
	color: rgb(73, 67, 55);
	padding: 15px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	font-family: 'Oxygen', sans-serif;
	letter-spacing: 2px;
	cursor: pointer;
  display:absolute;
  margin-bottom: 10px;
}

button:hover {
  background-color: #282753;
  border: none;
  border-radius: 8px;
  border-bottom: 4px solid #64090a;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-family: 'Oxygen', sans-serif;
  letter-spacing: 2px;
  margin-bottom: 10px;
}

button:active {
  background-color: #ff8e0d;
  border: none;
  border-radius: 8px;
  border-bottom: 4px solid #000000;
  color: rgba(255, 255, 255, 0.918);
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 17px;
  font-family: 'Oxygen', sans-serif;
  letter-spacing: 2px;
  margin-bottom: 10px;
}

.links:hover{
 color:rgb(0, 22, 43);
 text-decoration: underline;
}
.fas:hover{
  color:rgba(145, 160, 173, 0.726);
  text-decoration: underline;
 }
 ul{
   list-style: none;
 }
  .h1_links:hover{
  color: #000;
 }

 /*media*/

 @media (max-width:980px){
  .sidebar{
    width:106vw;
  }
  .h1_links{
    font-size:29px;
  }
 }


@media (max-width:768px){
  .sidebar{
    width:106vw;
  }
  .h1_links{
    font-size:28px;
  }
  .links{
display:none;
  }
}
 
@media (max-width:370px){
  .sidebar{
    width:106vw;
  }
  .h1_links{
    font-size:24px;
  }
  .links{
    font-size: 16px;
  }
}